@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
  }
  *
  {
  margin:0;
  padding: 0;
  }
  body
  {
  font-family: 'Raleway';
  }
  .App
  {
  display: flex;
  width: 100vw;
  min-height: 100vh;
  }
  /* .condiv class is for the right side content container*/
  .condiv
  {
  padding: 100px ;
  background-color: #f4f5f9;
  flex:6 1;
  flex:var(--condiv-flex) 1;
  }

  nav
{
padding:60px 30px 0px 30px ;
flex:1.5 1;
flex:var(--sidebar-flex) 1;
background: rgb(238,103,143);
background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgba(238,103,143,1)), color-stop(80%, rgba(132,124,252,1)));
background: linear-gradient(180deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
}

nav ul
{
  font-size:1.6rem;
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none !important;
  color: white !important;
}
nav ul li
{
  margin:40px;
}
nav ul li a{
  text-decoration: none;
  color: white;
}
/* css for active class */
.active
{
font-weight: bolder;
}

.profilepic{
  width: 200px;
  height: 200px;
  display: block;
  margin: auto;
  border-radius: 50%;
}
.typingeffect{
  font: 50px;
}

.social{
  position: absolute;
  margin-top: 20px;
  display: flex;
  bottom: 60px;
}

.social i{
  padding-right: 30px;
  font-size: 25px;
  color: #9a9b9c;
}

.social i:hover{
  color: white;
}

.subtopic{
  margin: 10px 10px 30px 10px !important;
}

p{
  font-size: 1.2rem;
  font-size: var(--fontsize-p);
}

h1, h2, h3, h4, p{
  margin: 10px;
}

.widecard{
  border: 0px solid #9a9b9c;
  display: flex;
  margin: 30px 0px 30px 0px;
}

.widecard:hover{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
